import initEasyDimension from './init'
import line from './line/index'
import cure from './cure/index'
import pie from './pie/index'


class EasyDimension {
    constructor() {
        this.ctx = ''
    }
    // canvas 
    detectCtx() {
        if(!this.ctx) {
            console.error('canvas easyMark.init()!')
            return false
        }
        return true
    }
    // canvas 
    init(domId) {
        this.ctx = initEasyDimension(domId)
    }
    // 
    drawLine(option) {
        if(this.detectCtx()){
            line(this.ctx, option)
        }
    }
    // 
    drawCure(option) {
        if(this.detectCtx()) {
            cure(this.ctx, option)
        }
    }
    // 
    drawPie(option) {
        if(this.detectCtx()) {
            pie(this.ctx, option)
        }
    }
}


export default EasyDimension