
/**
 * 
 * @param {array} arr 2
 * @param {string} text console
 */
const illegalConsole = (arr, text) => {
    if(!arr) {
        console.error(text)
        return true
    }
    if(!(arr instanceof Array)) {
        console.error(text)
        return true
    }
    if(arr.length != 2) {
        console.error(text)
        return true
    }
    return false
}

export default illegalConsole