import merge from 'loadsh/merge'

import illegalConsole from './util'
/**
 * 
 * @param {} ctx canvas
 * @param {object} option 
 */
const pie = (ctx, option) => {
    const {
        // ,[30, 40]
        center,
        // , [ 40, 50]
        radius,
        // 3
        startAngle = Math.PI * 0,
        // 
        endAngle = Math.PI * 2,
        // false 
        // *** 
        clockwise = true
    } = option
    // 
    if(illegalConsole(center, '[100, 100]')){
        return
    }
    // 
    if(!radius || typeof radius != Number) {
        console.error('')
    }
    // 
    // 
    const periphery = {
        // 
        show: false,
        // 
        color: 'red',
        // 
        lineWidth: 2,
        // 
        lineDash: []
    }
    merge(periphery, option.periphery)
    // 
    const fill = {
        show: true,
        color: 'red'
    }
    merge(fill, option.fill)
    ctx.beginPath()
    // 
    ctx.setLineDash(periphery.lineDash)
    ctx.lineWidth = periphery.lineWidth
    ctx.strokeStyle = periphery.color
    // 
    ctx.fillStyle = fill.color
    // 
    ctx.arc(center[0], center[1], radius, startAngle, endAngle, clockwise)
    if(periphery.show) {
        ctx.stroke()
    }
    if(fill.show) {
        ctx.fill()
    }
}

export default pie